import { useEffect, useState } from "react";
import api from "../utils/api";
import Link from "next/link";

export default function Home() {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    loadPosts();
  }, []);

  const loadPosts = async () => {
    try {
      const res = await api.get("/posts");
      setPosts(res.data);
    } catch (err) {
      console.error("Failed to load posts");
    }
  };

  const handleBuy = (id) => {
    const token = localStorage.getItem("token");
    if (!token) {
      alert("Please login first to buy!");
      return;
    }
    // এখানে চাইলে payment বা order logic বসানো যাবে
  };

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <h1 className="text-3xl font-bold mb-6 text-center">Trust Market</h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {posts.map((post) => (
          <div key={post._id} className="bg-white p-4 rounded-xl shadow">
            <h3 className="text-lg font-bold">{post.title}</h3>
            <p>{post.description}</p>
            <p className="text-blue-600 font-semibold mt-2">${post.price}</p>

            <button
              onClick={() => handleBuy(post._id)}
              className="bg-green-600 text-white w-full py-2 mt-3 rounded hover:bg-green-700"
            >
              Buy
            </button>
          </div>
        ))}
      </div>

      <div className="text-center mt-10">
        <Link href="/login" className="text-blue-500 underline">
          Go to Login
        </Link>
      </div>
    </div>
  );
}
